import {Component} from '@angular/core'
export class App {
  databases: Array<any> = [];

  constructor() {
    var load = () => {
      this.databases = (<any>window).ENV.generateData(true).toArray();
      (<any>window).Monitoring.renderRate.ping();
      setTimeout(load, (<any>window).ENV.timeout);
    };
    load();
  }
}


